import React, {FormEvent, FunctionComponent, useState} from 'react';
import classNames from 'classnames';
import ClassNames from './TeaserForm.module.scss';

interface Props {
    heading: string;
    namePlaceholder: string;
    emailPlaceholder: string;
    submitButtonText: string;
}

const TeaserForm: FunctionComponent<Props> = ({heading, namePlaceholder, emailPlaceholder, submitButtonText}) => {
    const [nameError, setNameError] = useState(false);
    const [emailError, setEmailError] = useState(false);

    // lame.
    function sendForm(event: FormEvent<HTMLFormElement>) {
        event.preventDefault();
        const formData = new FormData(event.target as any); //todo tmp
        setNameError(!formData.get('name'));
        setEmailError(!formData.get('email'));
        if (!!formData.get('name') && !!formData.get('email')) {
            alert('send...');
        }
    }

    return (
        <form className={ClassNames.TeaserForm} onSubmit={sendForm}>
            <p>{heading}</p>
            <input name="name" placeholder={namePlaceholder} className={classNames(nameError && ClassNames.inputError)}/>
            <input name="email" placeholder={emailPlaceholder} className={classNames(emailError && ClassNames.inputError)}/>
            <button className={ClassNames.submit} type='submit'>{submitButtonText}</button>
        </form>
    );
};

export default TeaserForm;
