import React, {FunctionComponent} from 'react';
import ClassNames from './PageRoot.module.scss';
import classNames from 'classnames';
import {CmsPageMetaContext} from '../../../../types/CmsPageMetaContext';
import SEO from '../../../seo';
import Navigation from '../../../Navigation/Navigation';

interface Props {
    withNavigation?: boolean;
}

const PageRoot: FunctionComponent<Props & CmsPageMetaContext> = (
    {
        children,
        metaKeywords,
        metaDescription,
        metaTitle,
        withNavigation = true,
    }) => {
    return (
        <div className={classNames(ClassNames.Page, !withNavigation && ClassNames.noNav)}>
            <SEO title={metaTitle} description={metaDescription} keywords={metaKeywords}/>
            {withNavigation && <Navigation/>}
            {children}
        </div>
    );
};

export default PageRoot;
