import React, {FunctionComponent} from 'react';
import ClassNames from './TeaserPage.module.scss';
import {CmsPageContext} from '../../../../types/CmsPageContext';
import PageRoot from '../PageRoot/PageRoot';
import MobileImage from "./MobileImage/MobileImage";
import Media from "./Media/Media";
import TeaserForm from "./TeaserForm/TeaserForm";
import TeaserLanguageSelector from "./TeaserLanguageSelector/TeaserLanguageSelector";
import Helmet from "react-helmet";

interface Props {
    pageContext: {
        fields: {
            heading: string;
            summary: string;
            contactLine: string;
            brandName: string;
            socialLine: string;
            twitterUrl?: string;
            facebookUrl?: string;
            instagramUrl?: string;
            formSubmitButtonText: string;
            formNamePlaceholder: string;
            formEmailPlaceholder: string;
        };
    };
}

const TeaserPage: FunctionComponent<Props & CmsPageContext> = props => {
    const {
        heading,
        summary,
        contactLine,
        brandName,
        socialLine,
        twitterUrl,
        facebookUrl,
        instagramUrl,
        formSubmitButtonText,
        formNamePlaceholder,
        formEmailPlaceholder,
    } = props.pageContext.fields;

    return (
        <PageRoot {...props.pageContext.meta} withNavigation={false}>
            <Helmet>
                <link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.8.1/css/brands.css"
                      integrity="sha384-n9+6/aSqa9lBidZMRCQHTHKJscPq6NW4pCQBiMmHdUCvPN8ZOg2zJJTkC7WIezWv"
                      crossOrigin="anonymous" />
                    <link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.8.1/css/fontawesome.css"
                          integrity="sha384-vd1e11sR28tEK9YANUtpIOdjGW14pS87bUBuOIoBILVWLFnS+MCX9T6MMf0VdPGq"
                          crossOrigin="anonymous" />
            </Helmet>
            <div className={ClassNames.TeaserPage}>
                <div className={ClassNames.area1}>
                    <div className={ClassNames.branding}>{brandName}</div>
                    <div className={ClassNames.imageWrapper}>
                        <MobileImage/>
                    </div>
                    <TeaserLanguageSelector className={ClassNames.languageSelector}/>
                </div>
                <div className={ClassNames.area2}>
                    <div className={ClassNames.content}>
                        <h1>{heading}</h1>
                        <p>{summary}</p>
                        <TeaserForm heading={contactLine}
                                    submitButtonText={formSubmitButtonText}
                                    namePlaceholder={formNamePlaceholder}
                                    emailPlaceholder={formEmailPlaceholder}/>

                        <p>{socialLine}</p>
                        <Media twitterUrl={twitterUrl} facebookUrl={facebookUrl} instagramUrl={instagramUrl}/>
                    </div>
                </div>
            </div>
        </PageRoot>
    );
};

export default TeaserPage;
